import React from "react";
import { StyledRoundedButton } from "./rounded-button.styles";
import { FaArrowUp } from "react-icons/fa";

const RoundedButton = () => {
  return (
    <div
      style={{
        position: "sticky",
        bottom: "2.5rem",
        width: "100%",
        display: "flex",
        justifyContent: "flex-end"
      }}
    >
      <StyledRoundedButton>
        <FaArrowUp />
      </StyledRoundedButton>
    </div>
  );
};

export default RoundedButton;
