import styled, { keyframes } from "styled-components";
import { theme } from "../../style/theme";
import { devices } from "../../style/devices";

const textclip = keyframes`
  to {
   background-position:200% center
  }
 `;

export const HomePageContainer = styled.div`
  background: ${theme.light};
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: ${theme.dark};
  min-height: 100%;
  z-index: 1;

  @media ${devices.laptop} {
    border-radius: 0.3rem;
    box-shadow: 0.1rem 0.1rem 2rem 0.5rem rgba(0, 0, 0, 0.2);

    width: 37%;
    min-width: 37%;
  }
  @media ${devices.widescreen} {
    width: 30%;
    min-width: 30%;
  }
`;

export const BackgroundContainer = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  background: url("https://i.pinimg.com/originals/44/6e/3b/446e3b79395a287ca32f7977dd83b290.jpg")
    center center / cover;
  clip-path: polygon(0 0, 100% 0, 100% 30%, 50% 37%, 0 30%);
  @media ${devices.tablet} {
    clip-path: polygon(0 0, 100% 0, 100% 27%, 50% 34%, 0 27%);
  }
`;

export const AvatarContainer = styled.div`
  width: 50%;
  z-index: 1;
  position: relative;
  bottom: 6%;
  img {
    border-radius: 50%;
    box-shadow: 0.5rem 0.5rem 1rem 0.3rem rgba(0, 0, 0, 0.1);
  }

  @media ${devices.tablet} {
    width: 35%;
  }
  @media ${devices.laptop} {
    width: 50%;
  }
`;

export const InfoContainer = styled.div`
  text-align: center;
  z-index: 1;
  position: relative;
  bottom: 4%;

  h1 {
    letter-spacing: 0.2rem;
    font-weight: 300;
  }

  a {
    margin: 0 0.3rem;
  }

  h3 {
    margin-top: 1rem;
  }
  @media ${devices.laptop} {
    bottom: 4%;
  }
`;
export const StyledTypist = styled.div`
  margin: 1rem;
  color: ${theme.light};
  font-weight: 300;
  background: linear-gradient(
    to right,
    ${theme.primaryColor} 0%,
    ${theme.secondaryColor} 25%,
    ${theme.dark} 50%,
    ${theme.neutralColor} 75%,
    ${theme.primaryColor} 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: ${textclip} 13s linear infinite;
  display: inline-block;
  h1 {
    font-size: 1.8rem;
  }
  p {
    font-size: 1rem;
    font-weight: bold;
  }
`;
export const DescriptionContainer = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  bottom: 1rem;
  /* border-top: 1px solid #777; */
  margin-top: 2rem;
  a {
    display: flex;
    font-size: 0.9rem;
    justify-content: center;
    font-weight: bold;
    margin: 1rem 0.6rem;
  }
  div {
    margin-right: 0.5rem;
  }
  h3 {
    vertical-align: top;
  }

  @media ${devices.mobile} {
    a {
      margin: 1rem;
    }
  }
  @media ${devices.laptop} {
    bottom: 0;
  }
`;
