import React from "react";
import {
  FaFacebookF,
  FaFileDownload,
  FaRegAddressCard,
  FaGithub
} from "react-icons/fa";
import {
  HomePageContainer,
  BackgroundContainer,
  InfoContainer,
  StyledTypist,
  DescriptionContainer,
  AvatarContainer
} from "./home-page.styles";
import TypingText from "../../components/typing-text/typing-text.component";
// import ArrowDownButton from "../../components/arrow-down/arrow-down.component";
import { Link } from "react-scroll";

const HomePage = props => {
  return (
    <HomePageContainer>
      <AvatarContainer>
        {console.log(window.scrollY)}
        <img src="/avatar.jpg" alt="avatar" />
      </AvatarContainer>
      <InfoContainer>
        <StyledTypist>
          <h1>TRẦN NGUYÊN</h1> <p>Front-end Developer</p>
        </StyledTypist>
        <h3>
          <a href="facebook.com">
            <FaFacebookF />
          </a>
          <a href="facebook.com">
            <FaGithub />
          </a>
        </h3>
      </InfoContainer>
      <TypingText infinite="8">It will never be perfect...</TypingText>
      <TypingText delay="5000ms">Stop thinking and make it better</TypingText>
      <Link
        activeClass="active"
        to="detailContainer"
        offset={-60}
        spy={true}
        smooth={true}
        duration={800}
        containerId="containerElement"
      >
        {/* <ArrowDownButton /> */}
      </Link>
      <DescriptionContainer>
        <a href="https://google.com">
          <div>DOWNLOAD CV</div>
          <h3>
            <FaFileDownload />
          </h3>
        </a>
        <a href="https://google.com">
          <div>CONTACT ME</div>
          <h3>
            <FaRegAddressCard />
          </h3>
        </a>
      </DescriptionContainer>
      <BackgroundContainer />
    </HomePageContainer>
  );
};

export default HomePage;
