import React from "react";
import "./App.styles";
import { Switch, Route } from "react-router-dom";
import HomePage from "./pages/home-page/home-page.component";
import AboutPage from "./pages/about-page/about-page.component";
import BlogPage from "./pages/blog-page/blog-page.component";
import ContactPage from "./pages/contact-page/contact-page.component";
import ResumePage from "./pages/resume-page/resume-page.component";
import WelcomePage from "./pages/welcome-page/welcome-page.component";
import Navbar from "./components/navbar/navbar.component";
import Footer from "./components/footer/footer.component";
import { AppContainer, PageContainer, NavPageContainer } from "./App.styles";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import { Link, Element } from "react-scroll";
import PageWrapper from "./hoi/page-wrapper/page-wrapper.component";
import { SlideWrapper } from "./hoi/slide-wrapper/slide-wrapper.styles";
import "./stylestest.css";

function App(props) {
  return (
    <AppContainer>
      <NavPageContainer>
        <Link
          activeClass="active"
          to="detailContainer"
          offset={-90}
          spy={true}
          smooth={true}
          duration={800}
          containerId="containerElement"
        >
          <Navbar />
        </Link>

        <PageContainer name="page" className="element" id="containerElement">
          <Element name="home" />
          <Route component={HomePage} />
          <PageWrapper>
            <Element name="detailContainer">
              <Route
                render={({ location }) => (
                  <TransitionGroup>
                    <CSSTransition
                      key={location.key}
                      classNames="fade"
                      timeout={1000}
                    >
                      <Switch location={location}>
                        <Route
                          exact
                          path="/"
                          render={() => (
                            <SlideWrapper>
                              <WelcomePage />
                            </SlideWrapper>
                          )}
                        />
                        <Route
                          exact
                          path="/about"
                          render={() => (
                            <SlideWrapper>
                              <AboutPage />
                            </SlideWrapper>
                          )}
                        />
                        <Route
                          exact
                          path="/blog"
                          render={() => (
                            <SlideWrapper>
                              <BlogPage />
                            </SlideWrapper>
                          )}
                        />
                        <Route
                          exact
                          path="/contact"
                          render={() => (
                            <SlideWrapper>
                              <ContactPage />
                            </SlideWrapper>
                          )}
                        />
                        <Route
                          exact
                          path="/resume"
                          render={() => (
                            <SlideWrapper>
                              <ResumePage />
                            </SlideWrapper>
                          )}
                        />
                      </Switch>
                    </CSSTransition>
                  </TransitionGroup>
                )}
              />
            </Element>
          </PageWrapper>
        </PageContainer>
      </NavPageContainer>
      <Footer />
    </AppContainer>
  );
}

